import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import Loading from './Loading'
import Carousel from 'react-bootstrap/Carousel';
export default function Productdetails() {
  let { id } = useParams()
  let [prod, setprod] = useState({})
  useEffect(() => {
    getProductData()
  }, [])
  const getProductData = async () => {
    let { data } = await axios.get(`https://skincare-92zr.onrender.com/product/by/${id}`)
    console.log(data.message)
    let newProd = { ...data.message }
    if (newProd.category == "Toners") {
      newProd["category"] = "Essence & Toner"
    } else if (newProd.category == "Cleansers") {
      newProd["category"] = "Mask & Cleanser"
    }
    setprod(newProd)
  }
  const [index, setIndex] = useState(0);

  const handleSelect = (selectedIndex) => {
    setIndex(selectedIndex);
  };
  let navto= useNavigate()
  return (<>
    {Object.keys(prod).length != 0 ? <div className=' mt-4 h-100 container'>
      <div className='d-flex flex-wrap p-3 co'>
        <div className='col-12 mb-4'>
          <h1 className='pink-text fs-2'>{prod.prodName}</h1>
        </div>
        <div className='col-sm-12 col-md-6 col-lg-4 p-3 '>
          <div className='col-12'>
            {prod.images[0] ?
              <Carousel activeIndex={index} onSelect={handleSelect} className='h-100'>
                {
                  prod.images.map(ele => {
                    return <Carousel.Item>
                      <img
                        className="d-block w-100"
                        src={ele.secure_url}
                        alt="First slide"
                      />
                    </Carousel.Item>


                  })
                }
              </Carousel>
              : <Loading />}
          </div>
        </div>
        <div className='col-sm-12 col-md-6 pt-3 ps-3'>
          <div>
          <h2 className='fs-5 pink-text'>Brand</h2>
            <p className='fs-6 make-cursor' onClick={()=>{navto(`/all?brand=${prod.brand}`)}}>{prod.brand}</p>   
          </div>
          <div>
            <h2 className='fs-5 pink-text'>Description</h2>
            <p className='fs-6'>{prod.prodDescription}</p>
          </div>
          <div className='col-12'>
            <h2 className='fs-5 pink-text'>Price : <span className='fs-6 text-dark fw-light'>{prod.price} EGP</span></h2>
            <h2 className='fs-5 pink-text'>Category : <span className='fs-6 text-dark fw-light'>{prod.category}</span></h2>
          </div>
        </div>
      </div>
    </div> : <div className='col-12 h-100 d-flex justify-content-center align-items-center'><Loading /></div>}</>
  )
}
