import React, { useEffect, useState } from 'react'

export default function Ordercard({ isAdmin}) {


    return (
        <div className='col-12 p-2 '>
            <div className='col-12 d-flex card-bg p-2 rounded rounded-3 '>
                <div className='col-10 col-md-11 ps-2 '>
                    <p className='pink-text m-0 fw-bold m-0'>Order ID : <span className='text-dark fw-light fs-6 '><small>1234567891234567891234</small></span></p>
                    <p className='pink-text m-0 fw-bold m-0'>Total price : <span className='text-dark fw-light'>300 EGP</span></p>
                    <p className='pink-text m-0 fw-bold m-0'>State : {isAdmin ? <></> : <span className='text-dark fw-light'>Pending</span>}
                    </p>
                    {isAdmin ? <> <select class="form-select">
                        <option value="pending">Pending</option>
                        <option value="accepted">Accepted</option>
                        <option value="shipping">Shipping</option>
                        <option value="canceled">Canceled</option>
                        <option value="done">Done</option>
                    </select>
                        <div className='col-12 d-flex justify-content-end p-2'>
                            <button className='btn border product-btn'><span className='pink-text'>save</span></button>
                        </div></>
                        : <></>}

                </div>
                <div className=' p-2 d-flex align-items-center justify-content-center make-cursor '>
                    <i class="fa-solid fa-chevron-right"></i>
                </div>
            </div>
        </div>

    )
}
